//1: 加载所有游戏部件 与 动画
//2: 存储信息并进行前后端总体交互

const CardRegion = require('js_cardRegion');
const DataRegion = require('js_dataRegion');


var game =  cc.Class({
    properties:{
    playerId:0,
    curCardId:0,
    storyId:0,
    sellected:'0',
    }
})

 var data = cc.Class({
    properties:{
        name : "data",
        budget:4 ,
        resource:3 ,
        health: 2,
        popularity:1
    }
})

cc.Class({
    extends: cc.Component,
    properties: {
        homeAnim: {  //动画
            default: null,
            type: cc.Animation
        },
        cardRegion: {  //卡片区域
            default: null,
            type: CardRegion
        },
        dataRegion: {  //数值区域
            default: null,
            type: DataRegion
        },
        backStage: { //后端
            default: null,
            type: cc.Node
        },
    },

     onLoad:function () {
        //请求后端加载

        //。。。
        //加载完成后得到
        
        
        
        
     },

    start:function () {
        this.dataRegion.updateInfo(this.information.dataInfo);
        this.cardRegion.updateInfo(this.information.cardInfo);
    },





    acceptCheck : function(  ) {

        this.information.dataInfo.budget += 1;
        this.information.dataInfo.resource += 2;
        this.information.dataInfo.health += 3;
        this.information.dataInfo.popularity += 4;

       this.dataRegion.updateInfo(this.information.dataInfo);
    },

    declineCheck : function(  ) {

        this.information.dataInfo.budget -= 1;
        this.information.dataInfo.resource -= 2;
        this.information.dataInfo.health -= 3;
        this.information.dataInfo.popularity -= 4;

       this.dataRegion.updateInfo(this.information.dataInfo);
    }
    
    
}
);
